@import "../../styles/variables";

.fav-panel {
	i {
		font-size: 7rem;
		padding: 0 1rem;
		transition: .2s;
		&.fa-times {
			font-size: 8rem;
			&:hover {
				transform: scale(.8);
				cursor: pointer;
			}
		}
		&.fa-info {
			color: cornflowerblue;
			&:hover {
				transform: scale(1.1);
				cursor: pointer;
			}

		}
		&.fa-heart {
			color: coral;
			&:hover {
				transform: scale(1.2);
				cursor: pointer;
			}
		}
		&.fa-envelope-o {
			display: none;
		}
	}
	&.invisible {
		filter: blur(8px);
	}
}

@media screen and (min-width: 481px) and (max-width: 1024px) {
	.fav-panel {
		i {
			font-size: 13rem;
			padding: 0 2rem;
			transition: .2s;
			&.fa-times {
				font-size: 14rem;
			}
		}
	}
}

@media screen and (min-width: 1025px) {
	.fav-panel {
		height: 10rem;
		i {
			margin: 0 auto;
			font-size: 12rem;
			transition: .2s;
			&.fa-times {
				font-size: 13rem;
			}
			&.fa-info {
				display: none;
			}
		}
	}
}
